.dashboard {
  font-size: 16px;
  background: white;
  box-shadow: none;
  border-radius: 5px;
  text-align: left;
  text-align: center;
}

.infoDashboard {
  background: white;
  z-index: 10000;
}

generic-dashboard {
  background: rgba(255, 255, 255, 0.8);
}

.dashboard h5 {
  margin: 0 0 5px;
}

.dashboardDetails {
  font-size: 20px;
  line-height: 25px;
}

.dashboardDetails .icon {
  margin-bottom: 10px;
}

.icon {
  border: none;
  border-radius: 2px;
  padding: 6px;
  cursor: pointer;
}

.icon.active {
  background: #e0e0e0 !important;
  box-shadow: inset 0 2px 4px rgba(0,0,0,0.15) !important;
  filter: contrast(1.1);
}

.infoSection {
  font-size: 18px;
}

.okButton {
  padding: 5px 20px;
  border: none;
  background: #f5f5f5;
  border-radius: 5px;
}

.tooltipLabel {
  text-align: center;
  box-shadow: none !important;
  font-size: 10px;
  background-color: transparent;
  z-index: 100;
  border: none;
}

.tooltipLabel:before {
  border: none;
}

.labelDay {
  font-size: 10px;
}

.leafletLeft-center {
  position: fixed;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  background: white;
}

.mapLegend-mobile {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 13px !important;
  padding: 8px !important;
}

.mapLegend > div {
  box-sizing: border-box;
  padding: 5px;
}

.infoIcon {
  background-color: white;
}

.legendItem {
  padding-right: 7px;
  padding-left: 7px;
}

.desc {
  font-size: 14px;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  padding: 0;
}

.column {
  display: flex;
  flex-direction: column;
}

.column-30 {
  width: 25%;
}

.column-70 {
  width: 75%;
}

.item {
  padding: 5px;
  box-sizing: border-box;
}

.rightIcon {
  transform: rotate(180deg);
}

.navIcon {
  cursor: pointer;
}

.dashboardHeader {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* Remove default Leaflet control margins for the dashboard corner */
.leaflet-bottom.leaflet-right .leaflet-control {
  margin: 0 !important;
  box-shadow: none !important;
}

/* Aggressively remove shadows from all dashboard elements and icons */
.dashboard,
.dashboard *,
.icon,
.icon *,
.leaflet-control {
  box-shadow: none !important;
}

.rest-day-border-only {
  display: flex !important;
  align-items: center;
  justify-content: center;
}

.rest-day-circle {
  width: 100%;
  height: 100%;
  border: 3px solid;
  border-radius: 50%;
  box-sizing: border-box;
  background: white; /* Add white background to make the icon stand out */
  position: relative;
}
